<template>
    <div id="popup">
        <form action="#" id="form">
            <div id = "search_bar">
                <div id="search">
                    <label for="key">搜索</label><input id="key" type="text" v-model.lazy.trim="keyValue">
                </div>                       
                <button type="button" @click="isIndexDate=!isIndexDate">日期</button>
                <button type="button" @click="isIndexTag=!isIndexTag">标签</button>
            </div> 
            <hr v-show="isIndexDate">
            <div v-show="isIndexDate" id="date_bar">
                <label for="start">开始</label><input type="date" id="start" v-model="dateStart">
                <label for="end">结束</label><input type="date" id="end" v-model="dateEnd">
            </div>
            <hr v-show="isIndexTag"> 
            <div v-show="isIndexTag" id="tag_bar">
                <label for="tagselect">标签</label><select name="" id="tagselect" v-model="selectTag">
                    <option value="">请选择一个标签，进行筛选</option>
                    <option v-for="(item,index) of clipTags" :key="index" v-bind:value="item">{{item}}</option>
                </select>
            </div>
        </form>
        <ul>
            <div>
                <span class="index">No.</span><span class="content">内容</span><span class="tag">标签</span><span class="button">操作</span>
            </div>
            <li class="itemshow" v-for="(item,index) in showBoard" :key="item.id">
                <span class="index">{{index+1}}</span><span class="content" :title="item.content">{{item.content}}</span><span class="tag">{{item.tag=="default"?"默认":item.tag}}</span>
                <button type="button" id="delete" @click="deleteItem($event,item)">删除</button>
            </li>
        </ul>
    </div>            
</template>

<script>   
    export default {
        name: "PopUp",
        data(){
            return{
                clipBoard:"",
                clipTags:"",
                keyValue:"",
                selectTag:"",
                dateStart:this.getToday(),
                dateEnd:this.getToday(),
                isIndexDate:false,
                isIndexTag:false
            }
        },
        computed: {
            showBoard(){
                if(this.clipBoard=="")return;
                console.log("02");
                console.log(this.clipBoard);
                let arr = this.clipBoard.slice(1,this.clipBoard.length).filter((item)=>{
                   return item.content.indexOf(this.keyValue) !== -1;
                })
                if(this.isIndexDate){
                    let checkTime1 = Date.parse(this.dateStart)+new Date().getTimezoneOffset()*60*1000;
                    let checkTime2 = Date.parse(this.dateEnd)+new Date().getTimezoneOffset()*60*1000+24*60*60*1000;
                    arr = arr.filter((item)=>{
                        return item.id >= checkTime1 && item.id < checkTime2;
                    })    
                }
                if(this.isIndexTag&&this.selectTag){
                    arr = arr.filter((item)=>{
                        return item.tag == this.selectTag;
                    })    
                }
                return arr.reverse();; 
            }
        },
        methods: {
            getToday(){
                let date = new Date();
                let year = date.getFullYear();
                let month = date.getMonth()>8?date.getMonth()+1:"0"+(date.getMonth()+1);
                let day = date.getDate()>9?date.getDate():"0"+date.getDate();
                return year + "-" + month + "-" + day; 
            },
            deleteItem(event,item){
                this.clipBoard.splice(this.clipBoard.indexOf(item),1);
                this.setClipBoard();
            },
            async getClipBoard(){
                this.clipBoard = await new Promise((resovle)=>{
                chrome.storage.local.get(['clipData'], function(result){resovle(result.clipData)})
                })              
            },
            async getTags(){
                this.clipTags = await new Promise((resovle)=>{
                chrome.storage.local.get(['tags'], function(result){resovle(result.tags)})
                })
            },
            setClipBoard(){
                chrome.storage.local.set({clipData: this.clipBoard}, function(){}) 
            },
            setTags(){
                chrome.storage.local.set({clipData: this.clipTags}, function(){}) 
            },
        },
        created() {
            this.getClipBoard();
            this.getTags();   
        }
    }  
</script>

<style lang="less">
    @zimablue:rgb(91,194,231);
    * {
        margin:0px;
        padding: 0px;
        box-sizing: border-box;
        vertical-align: middle;
        font-family: PingFang SC,HarmonyOS_Regular,Helvetica Neue,Microsoft YaHei,sans-serif;
        font-size: 16px;
    }
    #popup {
        width:400px;
        height:500px;
        overflow-y: auto;    
        #form {
            margin:0px auto;
            width:396px;
            background-color: @zimablue;
            display: flex;
            flex-flow: column wrap;
            top: 0px;
            #search_bar {
                height: 30px;
                display: flex;
                flex-flow: row nowrap;
                align-items: center;
                padding: 0px 2px;
                font-weight: 700;
                #search {
                    flex:5;
                    height: 24px;
                    background-color: antiquewhite;
                    border-radius: 5px;
                    display: flex;
                    align-items: center;
                    overflow: hidden;
                    label {
                        padding: 0px 4px;
                    }
                    input {
                        outline:0px;
                        border: 0px;
                        background-color: inherit;
                        flex: 1;
                    }
                }
                button {
                    font-weight: inherit;
                    flex:1;
                    height:24px;
                    background-color: aliceblue;
                    border: 0px;
                    border-radius: 5px;
                    margin: 2px 4px;;
                }
            }
            #date_bar {
                margin: 0px auto;
                input {
                    margin: 3px;
                    background-color: aliceblue;
                    border: 0px;
                    outline: 0px;
                }
                #start {
                    margin-right: 10px;
                }
            }
            #tag_bar {
                margin: 0px auto;
                select {
                    margin: 3px;
                    background-color: aliceblue;
                    border: 0px;
                    outline: 0px;
                }
            }
            hr {
                width: 100%;
                height: 1px;
                border:0px;
                background-color: snow;
            }
        }
        ul {
            width:100%;
            list-style: none;          
            li {
                .items(18px,4,1,1);
                .index {  
                    &[selected] {
                        background-color: navy;
                        color: antiquewhite;
                    }
                    border:2px solid navy;;                
                    border-radius: 100%;                   
                    text-align:center;
                    margin: 0px 3px;
                    line-height: 14px;
                    font-size: 12px;
                }
                .tag {
                    text-align: center;
                    color: navy;
                }
                button {
                    font-size: 14px;
                }
            }
            &>div {
                .items(24px,4,1,1);
                text-align: center;
                background-color: azure;
            }
        }
        .items(@index,@content,@tag,@button){
            margin:0px 2px;
            height: 20px;
            line-height: 20px;
            display: flex;
            align-items: center;
            justify-content: stretch;
            overflow: hidden;
            .index {
                flex:0 0 @index;
            }
            .content {
                flex: @content;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }
            .tag {
                flex: @tag;    
            }
            button,.button {
                flex: @button @button 20px;
                border: 0px;
            }

        }

    }
    
</style>